* {
  margin: 0;
  padding: 0;
}

.overlay {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  text-align: center;
  /* visibility: hidden; */
  opacity: 1;
  transition: opacity 0.3s;
  justify-content: center;
}

.overlay img {
  max-width: 90%;
  max-height: 90%;
  min-width: 400px;
  width: auto;
  height: auto;
  transform: scale(0.95);
  transition: transform 0.3s;
}

.cursor {
  cursor: pointer;
}

#sidebar {
  display: flex;
}
#root {
  height: 100%;
}

.hideInWeb {
  display: none;
}

@media print {
  div,
  a,
  span,
  #root p {
    color: black;
  }
  .hideInPrintVersion {
    display: none;
  }
  .hideInWeb {
    display: block;
  }

  #sidebar {
    display: none;
  }
  #root {
    height: auto;
  }
  .vl {
    display: none;
  }
  .stepCard {
    page-break-before: always;
  }
  #closeButton {
    display: none;
  }
}
